import React from 'react';


export class BannerCanvas extends React.Component {
    canvasRef : React.RefObject<HTMLCanvasElement> = React.createRef();
    constructor(props:string) {
        super(props);
        this.canvasRef = React.createRef();
        this.state = {
            height : 800,
            width : 800
        };
        this.getHeight =this.getHeight.bind(this);
    }

    getHeight(){
        return this.state;
    }

    componentDidMount(): void {
        let canvas = this.canvasRef;
        let ctx: CanvasRenderingContext2D | null = canvas.current ? canvas.current.getContext('2d') : null;


        let size = 25;
        let x =0;
        let y =0;
        let offset = 3/2 * size;
        let offsety = Math.sqrt(3)*size;


        for(let z=0; z<5; z++) {
            for (let i = 0; i < 20; i++) {
                let temp;
                if (i % 2 === 0) {
                    temp = new Hexagon((x + (offset)) * i, y+(offsety*z), size, offset, "red");
                } else
                    temp = new Hexagon((x + (offset)) * i, (offsety*z)-(offsety / 2), size, offset, "blue");

                temp.draw(ctx);
            }
        }
    }

    render() {
        const canvasstyle = {
            backgroundColor: "white",
            width: "100%",
            height: "200px",
            display: "block"
        };

        return (
            <canvas ref={this.canvasRef} style={canvasstyle} />
        );
    };
}

class Hexagon {
    x:number;
    y:number;
    size:number;
    offset:number;
    color:string;

    constructor(x:number, y:number, size:number, offset:number, color:string) {
        this.x = x;
        this.y = y;
        this.size = size;
        this.offset = offset;
        this.color = color;
    }

    draw(ctx:any) {
        ctx.beginPath();
        //ctx.moveTo(this.x + this.size * Math.cos(0), this.y + this.size * Math.sin(0));
        ctx.fillStyle = this.color;
        ctx.fill();
        //ctx.lineWidth = 2;
        ctx.strokeStyle = "black";

        let sides = 6;
        for (let i=0; i<=sides; i++) {
            this.x = this.x + this.size * Math.cos(i*2*Math.PI/sides);
            this.y = this.y + this.size * Math.sin(i *2 * Math.PI/sides);
            ctx.lineTo(this.x, this.y);
            ctx.stroke();

            //console.log(this);
        }
        ctx.closePath();

        ctx.fill();
    }
}
